.left-pane  {
  padding: 10px 25px ;
  display: flex;
  flex-direction: column;
  align-items: start;
  height: 100vh;
  width: 300px;
  overflow-y: scroll;
  position:fixed;
  top: 0;
}



.left-pane .logo img {
    height: 40px;
}


.left-pane link {
    color: #fff;
}

.left-pane .box {
    color: #fff;
    display: flex;
    align-items: center;
    font-size: 22px;
    font-weight: bold;
    padding: 5px;
    margin-bottom: 6px;
    text-transform: capitalize;
    border-radius: 17px;
    padding: 5px 10px;
    cursor: pointer;
    transition: .3s ;
}

.left-pane .box:hover,
.left-pane .data-Personal:hover {
    background-color: #1f2124;
}

.left-pane .box .icon {
    margin-right: 10px;
    padding-top: 7px;
    color: #fff;
}

.left-pane .bottom {
    background-color: #1d9bf0;
    width: 204px;
    border-radius: 20px;
    padding: 11px 20px;
    font-size: 19px;
    font-weight: bold;
    text-align: center;
    cursor: pointer;
}

.left-pane .bottom-rs {
    height: 150px;
    width: 100%;
    display: flex;


    span {
        height: 50px;
        width: 50px;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #1d9bf0;
        cursor: pointer;
        display: none;
        transition: .2s;
    }
}

.left-pane .bottom:hover , .left-pane .bottom-rs span:hover{
    background-color: #128ada;
}

.left-pane .data-Personal {
    display: flex;
    width: 204px;
    margin-top: 20px;
    padding: 11px 20px;
    border-radius: 20px;
    cursor: pointer;
    transition: .3s;
}

.left-pane .data-Personal img {
    height: 40px;
    border-radius: 50%;
    margin-right: 10px;
}

.left-pane .data-Personal .text {
    display: flex;
    flex-direction: column;

    h4 {
        color: #fff;
    }

    p {
        color: #5e6266;
    }
}





@media (max-width:996px) {
    .left-pane {
        width: 200px;
    }

    .left-pane .box {
        border-radius: 50%;
        margin-top: 10px;
        width: 50px;
        height: 50px;
        position: relative;
    }

    .left-pane .box .icon {
        margin-right: 10px;
        padding-top: 7px;
        color: #fff;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }

    .left-pane .box p {
        display: none;
    }

    .left-pane .bottom {
        display: none;
    }

    .left-pane .bottom-rs span {
        display: flex;
    }



    .left-pane .data-Personal {
        border-radius: 50%;
        width: 60px ;
        height: 60px;
        position: relative;
    }

    .left-pane .data-Personal img {
        height: 40px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        
    }

    .left-pane .data-Personal .text {
        display: none;
    }
}


@media (max-width:866px) {
    .left-pane {
        width: 126px;
    }
}