.verified {


    .box__verified {
        width: 60%;
        margin: 35px auto 0;  
        display: flex;
        flex-direction: column;
        align-items: start;



        img{
            height: 150px;
            width: 300px;
        }

        h1 {
            color: #e7e9ea;
            margin-top: 20px;

        }

        p {
            color: #71767b;
            margin-top: 5px;

            a {
                font-weight: 500;
                color: #e7e9ea;
                border-bottom: 1px solid #e7e9ea ;
                
            }

            a:hover {
                border-bottom: 2px solid #e7e9ea ;
            }
        }
    }
}