// components style css 
.bottom-follow {
    background-color: #eff3f4;
    color: #000;
    font-weight: 500;
    min-width: 62px;
    height: 32px;
    border-radius: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    padding: 0 14px;
    cursor: pointer;
    transition: .3s;
}

.bottom-follow:hover {
    background-color: #d9dedf;
}


