.settingIcon span{
    margin-left: 8px;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
    cursor: pointer;


}

.settingIcon span:hover {
    background-color: #1f2124;
}