.messages {
    display: flex;
    position: relative;
    left: 300px;
    height: 100vh;



    .write__messages {
        width: 450px;
        height: 100%;
        border-right: 1px solid rgba(80, 85, 92, 0.5215686275);


        .top__write__messages {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 20px;

            p {
                font-size: 22px;
            }

            .icon{
                display: flex;
            }

            .icon span{
                margin-left: 8px;
                height: 40px;
                width: 40px;
                border-radius: 50%;
                padding: 5px;
                display: flex;
                align-items: center;
                justify-content: center;
                transition: 0.3s;
                cursor: pointer;


            }

            .icon span:hover {
                background-color: #1f2124;
            }
        }


        .content__write__messages {
            width: 100%;
            height: calc(104vh -  110px);
            overflow-y: scroll;
            display: flex;
            flex-direction: column;
            position: relative;
 

            .no-message {
                position: absolute;
                top: 40%;
                left: 35%;
            }

        }


        .itme {
            width: 100%;
            height: 90px;
            display: flex;
            align-items: center;
            padding: 13px 10px;
            cursor: pointer;
            transition: .3s;


            img {
                height: 50px;
                border-radius: 50%;
                margin-right: 10px;
            }

            .text {
                .name {
                    font-size: 15px;
                }

                .send {
                    font-size: 13px;
                    color: #71767b;
                }
            }
        }

        .itme:hover {
            background-color: #202224;
        }


    }



    .Select__messages {
        width: 600px;
        height: 100%;
        border-right: 1px solid rgba(80, 85, 92, 0.5215686275);
        display: flex;
        // align-items: center;
        justify-content: center;



        .content__Select__messages{
            margin: 30px auto 0;
            width: 400px;
            display: flex;
            flex-flow: column;
            align-items: center;
            justify-content: center;
           


            h2 {
                margin-top: 10px;
            }

            p {
                color: #71767b;
                margin-top: 5px;
                font-weight: 500;
            }

            bottom {
                background-color: rgb(29, 155, 240);
                width: 190px;
                text-align: center;
                padding: 16px;
                border-radius: 30px;
                font-weight: bold;
                cursor: pointer;
                display: block;
                margin-top: 26px;
                transition: .3s;
            }

            bottom:hover {
                background-color: rgb(20, 135, 212);
            }
        }


        
        .chat__Select__messages {
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .top {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 15px;
                border-bottom: 1px solid rgba(80, 85, 92, 0.5215686275);


                .box {
                  display: flex;
                  align-items: center;
                  gap: 10px;
                    img {
                        height: 45px;
                        border-radius: 50%;
                    }
                }

                .icon {
                    cursor: pointer;
                }
            }

            .chat-content {
                // background-color: #0095f6;
                height: 75%;
                padding-top: 25px;
                overflow-y: auto;
            }

            .data-center {
                display: flex;
                flex-direction: column;
                align-items: center;
                img {
                    height: 85px;
                    border-radius: 50%;
                }

                p {
                    margin: 10px 0;
                }

                bottom {
                    background-color: #363a3e;
                    padding: 5px 11px;
                    border-radius: 8px;
                    cursor: pointer;
                    transition: .3s;

                    a {
                        color: #fff;
                    }
                }

                bottom:hover {
                    background-color: #1f2124;
                }


            }


            .message {
                // background-color: rebeccapurple;
                justify-content: end;
                display: flex;
                padding: 10px 20px;
                flex-direction: column;
                align-items: end;
                

                span {
                    display: block;
                    width: fit-content;
                    background-color: #3797f0;
                    padding: 8px;
                    border-radius: 62px;
                    font-size: 14px;
                    margin-bottom: 10px;
                }
            }




            .bottom-input {
                height: 75px;
                display: flex;
                align-items: center;
                justify-content: center;

                form {
                    width: 98%;
                }


                .box__bottom-input {
                    width: 98%;
                    border-radius: 17px;
                    border: 1px solid rgba(80, 85, 92, 0.5215686275);
                    display: flex;
                    align-items: center;
                    padding: 4px 7px;
                }

                
                textarea {
                    padding: 5px;
                    width: 100%;
                    font-size: 15px;
                    color: #fff;
                    background-color: transparent;
                    outline: none;
                    border: 1px solid transparent;
                    resize: none;
                }


                .box-icons {
                    display: flex;
                }

                .icon {
                    margin: 0 5px;
                    cursor: pointer;
                }
            }

            input[type=submit] {
                background-color: transparent;
                border: 1px solid transparent;
                cursor: pointer;
                height: 100%;
                height: 30px;
                width: 65px;
                color: #3797f0;
            }

            input[type=submit]:focus {
                outline: none;
            }

        }
        
    }

}


.messages .hid {
    display: none;
}






//=============> style modal <=================// 
.Personal__modal, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    z-index: 10;
}

.overlay {
    background-color: rgb(253 253 253 / 30%);;
    -webkit-backdrop-filter: blur(1px);
    backdrop-filter: blur(1px);
}


.search {
    padding: 10px 0 0;
    max-width: 80vw;
    min-width: 500px;
    min-height: 377px;
    max-height: 90vh;
    border-radius: 16px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #000;
    width: 550px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    color: #fff;
    z-index: 11;



    .top__search {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 5px 15px 15px;
        border-bottom: 1px solid #1f2124;





        .close {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 25px;
            cursor: pointer;
            height: 35px;
            width: 35px;
            border-radius: 50%;
            transition: .3s;
        }

         .close:hover   {
            background-color: rgba(80, 85, 92, 0.5215686275);
        }



        p {
            font-weight: 500;
            cursor: pointer;
            margin-right: 10px;
            padding: 4px 15px;
            border-radius: 13px;
            transition: .3s;
        }
    }


    .to__search {
        border-bottom: 1px solid #1f2124;
        padding: 0 10px; 


        input {
            margin-left: 15px;
            height: 40px;
            border: 1px solid transparent;
            width: 90%;
            background-color: transparent;
            color: #fff;
        }

        input:focus{
            outline: none;
        }
    }


    .found {
        overflow-y: scroll;
        height: 200px;
        display: block;

        .no-found {
            padding: 8px  18px;
            p {
                color: #71767b;
            }
        }


        .people__found {
            display: flex;
            padding: 10px 20px;
            cursor: pointer;
            transition: .2s;
            position: relative;

            .box {
                display: flex;
                justify-content: space-between;
                width: 100%;
            }


            .content__people__found {
                display: flex;
                align-items: center;
                img {
                    border-radius: 50%;
                    height: 40px;
                    margin-right: 10px;
                }

                .text {
                    .name {
                        font-size: 17px;
                    }

                    .name-email {
                        color: #71767b;
                        font-size: 15px;
                    }
                }
            }


            input {
                width: 20px;
                cursor: pointer;
            }

            input::before {
                   content: "";
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background: transparent;
                            }


              

        }

        .people__found:hover {
            background-color: #1f2124;
        }
    }

    .chat {
        border-radius: 0 0 16px 16px;
        height: 65px;
        display: flex;
        align-items: center;
        border-top: 1px solid #1f2124;
        background-color: #000;
        z-index: 2000;
        position: relative;

        span {
            margin: auto;
            background-color: #1b4865;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 45px;
            width: 94%;
            border-radius: 9px;
            cursor:default;
        }

        span.Active {
            background-color: #0095f6;
        }
    }
  
}








@media (max-width:1120px) {
   .messages .write__messages {
        width: 380px;
    }

    .messages .Select__messages {
        width: 430px;
    }
}


@media (max-width:1111px) {
   .messages .write__messages {
        width: 280px;
    }

    .messages .Select__messages {
        width: 430px;
    }
}


@media (max-width:1011px) {
   .messages .write__messages {
        width: 180px;
    }

    .messages .Select__messages {
        width: 430px;
    }
}


@media (max-width:996px) {
    .messages {
        left: 200px;

    }

    .messages .write__messages {
        width: 130px;
    }

    .messages .Select__messages {
        width: 600px;
    }

    .messages .write__messages .top__write__messages {
        justify-content: center;
    }

    .messages .write__messages .top__write__messages p {
        display: none;
    }

    .content__write__messages p.no-message {
        display: none;
    }
}

@media (max-width:930px) {
    .messages .Select__messages {
        width: 500px;
    }
}


@media (max-width:930px) {
    .messages .Select__messages {
        width: 450px;
    }
}

@media (max-width:866px) {
    .messages {
        left: 126px;
    }
}

@media (max-width:780px) {
    .write__messages {
        display: none;
    }
}







