.notifications {
    position: sticky;
    top: 0;
    background-color: rgba(0, 0, 0, 0.65);
    -webkit-backdrop-filter: blur(12px);
    backdrop-filter: blur(12px);


    .top__notifications {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px;
    }
}




 .links__notifications {
    position: sticky;
    top: 60px;
    background-color: rgba(0, 0, 0, 0.65);
    -webkit-backdrop-filter: blur(12px);
    backdrop-filter: blur(12px);
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid rgba(80, 85, 92, 0.5215686275);


    a {
        display: block;
        color: #fff;
        width: 34%;
    }
    
    .box__links__notifications {
        cursor: pointer;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: .3s;


        p  {
            font-weight: 500;
        }
    }


    .box__links__notifications:hover {
        background-color: #1f2124;
    }
}