.postes {
    width: 100%;
    border-bottom: 1px solid #50555c85;
    padding:  5px;
    display: flex;
    cursor: pointer;
    



    .box__image {
        width: 10%;
        height: 100%;
        display: flex;
        justify-content: center;


        img {
            height: 40px;
            width: 40px;
            max-width: 100%;
            border-radius: 50%;
        }

    }



    .box__content {
        height: 100%;
        width: 90%;

        .data-post {
            display: flex;
            justify-content: space-between;
            align-items: center;



            .data {
                display: flex;
                align-items: center;
                position: relative;

                a {
                    color: #eff3f4;
                }

                h3 {
                    margin-right: 5px;
                    height: 24px;
                    transition: .3s;
                    position: relative;
                }

                h3:hover .hover-box {
                    opacity: 1;
                    display: block;
                }

                h3 .hover-box {
                    padding: 10px;
                    position: absolute;
                    background-color: #000;
                    width: 280px;
                    // height: 200px;
                    border-radius: 9px;
                    transition: .6s ;
                    top: 20px;
                    left: -10px;
                    border: 1px solid #71767b;
                    box-shadow: rgba(0, 0, 0, 0.4) 0px 30px 90px;
                    opacity: 0;
                    display: none;
                    z-index: 100;


                    .top {
                        display: flex;
                        justify-content: space-between;


                        img {
                            height: 50px;
                            border-radius: 50%;
                        }

                        .following-bottom {
                            display: flex;
                            align-items: center;
                            border-radius: 16px;
                            font-size: 14px;
                            height: 7px;
                            padding: 15px 10px;
                            background-color: #eff3f4;
                            // border: 1px solid #71767b;

                            span {
                                color: #000;
                               
                            }
                        }
                    }

                    p.email-name {
                        font-size: 13px;
                        margin-top: 4px;
                    }

                    p.dis {
                        font-size: 14px;
                        color: #fff;
                        margin-top: 10px;
                    }


                    .follow {
                        display: flex;
                        justify-content: space-between;
                        margin-top:10px ;


                        div {
                            display: flex;
                            font-size: 14px;

                            p {
                                color: #fff;
                            }
                        }
                    }
                
                }

                

                h3:hover.name {
                    border-bottom: 1px solid #fff;
                }




                p,span {
                    color: #71767b ;
                }

                span {
                    
                    margin: 0 3px;
                    font-weight: bold;
                    height: 25px;
                }

            }

        }


    }






    .content__text {


        p {
            font-size: 15px;
            
        }
    }

    .content__image {
        max-width: 500px;
        margin-top: 10px;


        img {
            border-radius: 9px;
            border: 1px solid #71767b;
            width: 100%;
            max-height: 500px;
        }


        .video {
            // border-radius: 9px;
            border: 1px solid #71767b;
            max-width: 100%;
            max-height: 500px;
        }
    }

}





@media (max-width:790px) {
    .postes .content__text p {
        font-size: 15px;
    }

    .postes .content__image {
        width: 404px;
    }

    .postes .box__content .data-post .data h3 {
        margin-right: 5px;
        font-size: 16px;
    }

    .postes .box__content .data-post .data p, .postes .box__content .data-post .data span {
        color: #71767b;
        font-size: 14px;
    }
}