
.overlayActions {
    background-color: transparent;
    opacity: 0;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    z-index: 10;
    cursor:auto;
}





.actions {

    display: flex;
    justify-content: space-between;
    margin-top: 10px;


    .box__action {
        display: flex;
        align-items: center;

        p {
            font-size: 15px;
            color: #71767b;
            // margin-left: 5px;
        }
    }

    span{

        width: 35px;
        height: 35px;
        border-radius: 50%;
        transition: 0.3s;
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
            fill: #71767b;
            
        }
    }


    .box__action {
        span.like svg.Active{
            fill: rgb(249, 24, 128) ;
        }
         p.Active{
            color: rgb(249, 24, 128) ;
        }

    }


    .last .bookmark svg.Active{
        fill: #1d9bf0;
    }


    .box__action:hover span.comment,
    .box__action:hover span.view ,
    span.bookmark:hover,
     span.share:hover{
        background-color: rgb(29 155 240 / 15%);;
    }

    .box__action:hover span.comment svg,
    .box__action:hover span.view svg,
     span.bookmark:hover svg,
     span.share:hover svg {
        fill: #1d9bf0;
    }

    .box__action:hover p.number__comment, .box__action:hover p.number__view {
        color: #1d9bf0;
    }





    .box__action:hover span.Repost{
        background-color: rgb(0 186 124 / 15%);
    }

    .box__action:hover span.Repost svg.parent {
        fill: rgb(0, 186, 124);
    }

    .box__action:hover p.number__Repost {
        color: rgb(0, 186, 124);
    }




    

    .box__action:hover span.like{
        background-color: rgb(249 24 128 /15%);
    }

    .box__action:hover span.like svg {
        fill: rgb(249, 24, 128);
    }

    .box__action:hover p.number__like {
        color: rgb(249, 24, 128);
    }






    .last {
        display: flex;
        gap: 5px;
    }
}


.actions .box__action {

    
    .Repost {
        position: relative;


        .menu {
            position: absolute;
            top: -8px;
            left: -75px;        
            border: 1px solid #000;
            background-color: #000;
            box-shadow: rgba(240, 240, 241, 0.25) 0px 50px 100px -20px, rgba(240, 240, 241, 0.25) 0px 30px 60px -30px, rgba(240, 240, 241, 0.25) 0px -2px 6px 0px , rgba(240, 240, 241, 0.25) 0px -2px 6px 0px ;
            width: 150px;
            border-radius: 9px;
            text-align: center;
            display: flex;
            flex-direction: column;
            justify-content: center;
            z-index: 20;
        }

    }


    
}


.share {
    position: relative;

    .menu-book {
        padding: 10px 0px;
        position: absolute;
        top: -8px;
        left: -180px;        
        border: 1px solid #000;
        background-color: #000;
        box-shadow: rgba(240, 240, 241, 0.25) 0px 50px 100px -20px, rgba(240, 240, 241, 0.25) 0px 30px 60px -30px, rgba(240, 240, 241, 0.25) 0px -2px 6px 0px , rgba(240, 240, 241, 0.25) 0px -2px 6px 0px ;
        width: 200px;
        border-radius: 9px;
        text-align: center;
        display: flex;
        flex-direction: column;
        justify-content: center;
        z-index: 20;

        p {
            border-radius: 9px 9px 0 0 ;
            color: #fff;
            padding: 10px;
            transition: .3s;
            margin: 0;
        }

        p:last-of-type {
            border-radius: 0 0  9px 9px  ;
        }

        p:hover {
            background-color: #4a50562b;
        }
    }

        .hid-book {
            display: none;
        }
}






//=============> style modal <=================//  



.Personal__modal, .overlay {
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: fixed;
    z-index: 10;
}

.overlay {
    background-color: rgb(253 253 253 / 30%);;
    -webkit-backdrop-filter: blur(1px);
    backdrop-filter: blur(1px);
}


.postComment {
    padding: 10px 0;
    max-width: 80vw;
    min-width: 600px;
    min-height: 300px;
    max-height: 90vh;
    border-radius: 16px;
    position: absolute;
    top: 5%;
    left: 50%;
    transform: translateX(-50%);
    background-color: #000;
    width: 550px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    color: #fff;
    z-index: 11;
    overflow-y: auto;



    .top__post {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 5px 15px;



        .close {
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 25px;
            cursor: pointer;
            height: 35px;
            width: 35px;
            border-radius: 50%;
            transition: .3s;
        }

        .close:hover, p:hover{
            background-color: #50555c85;
        }

        p {
            color: rgb(29, 155, 240);
            font-weight: 500;
            cursor: pointer;
            margin-right: 10px;
            padding: 4px 15px;
            border-radius: 13px;
            transition: .3s;
        }
    }


    .post__content {
        margin-top: 12px;
        padding: 5px 9px;
        display: flex;

        
    .box__image {
        width: 10%;
        height: 100%;
        display: flex;
        justify-content: center;


        img {
            height: 40px;
            border-radius: 50%;
        }
    }

        .data__post__content {
            display: flex;
            align-items: center;
            height: 20px;


            h2 {
                color: #e7e9ea;
                font-size: 20px;
            }

            p,span {
                color: #71767b;
            }


            .point {
                display: block;
                margin: 0 3px;
            }
        }

     
    }


    .text__postComment {
        padding: 5px 9px;
        width: 90%;
        margin: auto;
        border-left: 2px solid #71767b;



        .text {
            // border-left: 1px solid #71767b;
            direction: rtl;
            line-height: 30px;
            padding-left: 20px;
        }


        .Replying {
            margin: 15px ;
            color: #71767b;


            span {
                color: #1d9bf0;
            }
        }
    }






    .everyone {
        width: 98%;
        margin: auto;
        border-bottom: 1px solid rgba(80, 85, 92, 0.5215686275);
        padding-bottom: 10px;
       
       
        span {
        font-size: 16px;
        cursor: pointer;
        padding: 0px 9px ;
        color: rgb(29, 155, 240);
        display: flex;
        align-items: center;
        font-weight: 500;
        width: fit-content;
        border-radius: 30px;
        transition: .3s;


        p {
            margin-top: -5px;
        }
        }


        span:hover {
            background-color: rgba(80, 85, 92, 0.5215686275);
        }
      
        svg {
            fill:rgb(29, 155, 240);
            margin-right: 1px;
        }
    }




    .last {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px 15px 0px;

        .icons {
            display: flex;

            span {
            width: 30px;
            height: 30px;
            cursor: pointer;
            color: rgb(29, 155, 240);
            justify-content: center;
            align-items: center;
            font-weight: 500;
            border-radius: 30px;
            transition: .3s;
            margin-right: 15px;
            display: flex;
            }

            span:hover {
                background-color: rgba(80, 85, 92, 0.5215686275);
            }

        }


        svg {
             fill:rgb(29, 155, 240);
        }

        .icons {
            width: 90%;
        }


        input {
            cursor: pointer;
            padding:10px 20px;
            background-color: #1d9bf0;
            border: 1px solid #1d9bf0;
            color: #fff;
            border-radius: 19px;
            font-size: 16px;
        }
    }
  
}






